const dormitory = {
  namespaced: true,
  state: {
    currentFloor: null,
    chosenRoomId: null,
    roomObj: null,
    //左侧被激活的团体id
    activeTeamId: null,
    
    //房态列表
    roomStateList: []
  },
  getters: {
    roomStateDict(state) {
      const obj = {};
      state.roomStateList.forEach(a => {
        obj[a.code] = a.val
      })
      return obj
    }
  },
  mutations: {
    currentFloor(state, val){
      state.currentFloor = val;
    },
    chosenRoomId(state, val){
      state.chosenRoomId = val
    },
    roomObj(state, val){
      state.roomObj = val
    },
    activeTeamId(state, val){
      state.activeTeamId = val
    },
    changeRoomStateList(state, val){
      state.roomStateList = val
    }
  },
  actions: {}
}

export default dormitory